import { DatasetScope } from "@rp/common/api-types"

const getDatasetFilter = (scope: DatasetScope | undefined, watchlistPrompts?: boolean) => {
  if (
    !scope ||
    scope === "files" ||
    scope === "quartr_pro" ||
    scope === "market_narratives" ||
    scope === "middle_eastern_markets" ||
    scope === "crypto_markets" ||
    scope === "financial_times" ||
    scope === "expert_networks" ||
    watchlistPrompts
  ) {
    return [] as const
  }

  return [scope] as const
}

export { getDatasetFilter }
